@font-face {
    
font-family: 'switzer';
    src: url('@vercel/turbopack-next/internal/font/local/font?{%22path%22:%22./fonts/Switzer-Variable.woff2%22,%22preload%22:true,%22has_size_adjust%22:true}') format('woff2');
    font-display: swap;
    
font-weight: 300 400 500 600 700;
